//internal
import TopBar from "../Components/TopBar";
import BaseLayout from "../Layout/BaseLayout";
import { ServicesTitleContent, ServicesImage, ServicesContainer, ServicesStepContainer, ServicesStep, AccordionContainer, Ads } from "../Layout/ServicesLayout";

const SeoPage = () => {
  const steps = [
    {id:1, step:" ", title:"  ", subtitle:"    "},
    {id:2, step:" ", title:" ", subtitle:"   "},
    {id:3, step:" ", title:" ", subtitle:"  "},
    {id:4, step:" ", title:"  ", subtitle:"   "},
    {id:5, step:" ", title:"  ", subtitle:"  "},
    {id:6, step:" ", title:"   ", subtitle:"      "},
  ];
  const faqs = [
    {id:1, title:"       ", content:"             .                .               .                            6  12    .                           .                       ."},
    {id:2, title:"   ", content:"                           .                             .                ."},
    {id:3, title:"      ", content:"               .                .      Google Keyword Planner          .               .           ."},
    {id:4, title:"        ", content:"          .                       .        ALT                   .                         ."},
  ];
  return(
    <BaseLayout>
      <TopBar
        title=""
      />
      <div className="child:BaseContainer space-y-28">
        <ServicesTitleContent
        title=" "
        content=" (SEO)                   .            .                    .              ."
        />
        <ServicesContainer>
          <ServicesTitleContent
          title="     "
          content=" (  )                .                          .                           .           .                        .                                 .                          .                  ."
          />
          <ServicesImage
            src="assets/images/vectors/seo/seo-1.svg"
            props="!ml-auto"
          />
        </ServicesContainer>
        {/* Ads */}
        <Ads />
        <ServicesContainer>
          <ServicesImage
            src="assets/images/vectors/seo/seo-2.svg"
            props="!ml-auto"
          />
          <ServicesTitleContent
          title="   "
          content="                    .                     .                      .                     .                                          ."
          />
        </ServicesContainer>
        <ServicesContainer>
          <ServicesTitleContent
          title="   "
          content="                      .                              .                               .                                  .                            .                                  .                           ."
          />
          <ServicesImage
            src="assets/images/vectors/seo/seo-3.svg"
            props="!ml-auto"
          />
        </ServicesContainer>
        <ServicesStepContainer>
          {
            steps.map
            (item=>
            <ServicesStep
              key={item.id}
              step={item.step}
              title={item.title}
              subtitle={item.subtitle}
            />  
            )
          }
        </ServicesStepContainer>
        <AccordionContainer
          title=" "
          items={faqs}
        >
        </AccordionContainer>
      </div>
    </BaseLayout>
  );
}

export default SeoPage;