//internal
import TopBar from "../Components/TopBar";
import BaseLayout from "../Layout/BaseLayout";
import { ServicesTitleContent, ServicesImage, ServicesContainer, ServicesStepContainer, ServicesStep, AccordionContainer, Ads } from "../Layout/ServicesLayout";

const WebDesignPage = () => {
  const steps = [
    {id:1, step:" ", title:"  ", subtitle:"    "},
    {id:2, step:" ", title:"  ", subtitle:"   "},
    {id:3, step:" ", title:"  ", subtitle:"  "},
    {id:4, step:" ", title:"   ", subtitle:"   "},
    {id:5, step:" ", title:"  ", subtitle:"    "},
    {id:6, step:" ", title:"   ", subtitle:"     "},
  ];
  const faqs = [
    {id:1, title:"        ", content:"                                      ."},
    {id:2, title:"        ", content:"                    .           ."},
    {id:3, title:"      ", content:"                   .                 ."},
    {id:4, title:"        ", content:"                          ."},
    {id:5, title:"      " , content:"            .                      ." },
    {id:6, title:"         " , content:"               (  )            ." },    
    {id:7, title:"       " , content:"               .             ." },    
  ];
  return(
    <BaseLayout>
      <TopBar
        title=" "
      />
      <div className="child:BaseContainer space-y-28">
        <ServicesTitleContent
        title=" "
        content="  ()                 ( www.example.com)    .                    .             .                            .                             .       (CMS)                .                        ."
        />
        <ServicesContainer>
          <ServicesTitleContent
          title="   "
          content="                     .                      .                      .                                      ."
          />
          <ServicesImage
            src="assets/images/vectors/webdesign/site-1.svg"
            props="!ml-auto"
          />
        </ServicesContainer>
        {/* Ads */}
        <Ads />
        <ServicesContainer>
          <ServicesImage
            src="assets/images/vectors/webdesign/site-2.svg"
            props="!ml-auto"
          />
          <ServicesTitleContent
          title="   "
          content="                               .                   .             SSL             .                .                .                           ."
          />
        </ServicesContainer>
        <ServicesContainer>
          <ServicesTitleContent
          title="     "
          content="            .              .              .             .               .                        .                    .                           ."
          />
          <ServicesImage
            src="assets/images/vectors/webdesign/site-3.svg"
            props="!ml-auto"
          />
        </ServicesContainer>
        <ServicesStepContainer>
          {
            steps.map
            (item=>
            <ServicesStep
              key={item.id}
              step={item.step}
              title={item.title}
              subtitle={item.subtitle}
            />  
            )
          }
        </ServicesStepContainer>
        <AccordionContainer
          title=" "
          items={faqs}
        >
        </AccordionContainer>
      </div>
    </BaseLayout>
  );
}

export default WebDesignPage;