function AboutText({title,contet,align,image}){
  return(
    <section dir={align} className={`grid grid-cols-1 lg:grid-cols-2 BaseContainer`}>
      <div dir="rtl" className="flex flex-col gap-2 items-start justify-center">
        <h2 className="text-MainColor-50 font-MorabbaMedium text-3xl">{title}</h2>
        <p className="font-PinarMedium text-justify leading-8">{contet}</p>
      </div>
      <div className="w-full flex justify-end">
        <img src={image} className="h-[400px] mx-auto lg:mx-0 ltr:lg:ml-auto rtl:lg:mr-auto" alt="  " />
      </div>
    </section>
  );
}

function AboutGallery(){
  return(
    <section className="grid grid-cols-2 md:grid-cols-3 gap-4 BaseContainer">
      <img className="AboutImages" src="assets/images/About/4.webp" alt=" " />
      <img className=" AboutImages md:col-span-2" src="assets/images/About/1.webp" alt=" "  />
      <img className="AboutImages md:col-span-2" src="assets/images/About/3.webp" alt=" " />
      <img className="AboutImages" src="assets/images/About/2.webp" alt=" " />
    </section>
  );
}

